import React from 'react';
import { useTranslation } from 'react-i18next';
import { LinkBlank } from '@mssgme/ui';
import styles from './SettingsMenuFooter.scss';

export const PRIVACY_POLICY_URL = 'https://mssg.me/privacy-policy';
export const TERMS_OF_USE_URL = process.env.REACT_APP_TARGET === 'mobile' ?
    'https://www.apple.com/legal/internet-services/itunes/dev/stdeula/' :
    'https://mssg.me/terms-of-use';

export default function SettingsMenuFooter() {
    const { t } = useTranslation();

    return (
        <p className={styles.root}>
            <LinkBlank href={PRIVACY_POLICY_URL}>
                {t('settingsPage.privacy')}
            </LinkBlank><br />
            <LinkBlank href={TERMS_OF_USE_URL}>
                {t('settingsPage.terms')}
            </LinkBlank>
        </p>
    );
}
